import {mapState} from 'vuex';
import chatName from '@/components/chats/assets/name.vue'
import chatIcon from '@/components/chats/assets/icon.vue'
import userView from '@/components/user/view/pnuser/index.vue'
import {Swiper, SwiperSlide} from 'vue-awesome-swiper'
import 'swiper/swiper-bundle.css'
//
import {Carousel, Slide} from 'vue-carousel';
import f from '@/application/functions.js'
//

export default {
  name: 'chatPreview',
  props: {
    chat: Object,
    usersinfo: Array,
    room: {}
  },

  components: {
    userView,
    chatName,
    chatIcon,
    Swiper,
    SwiperSlide,
    Carousel,
    Slide
  },

  data: function () {

    return {
      loading: false,
      usersinfoLocal: [],
      swiperOption: {
        direction: 'horizontal',
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      }
    }

  },

  computed: mapState({
    auth: state => state.auth,
    roomMembers: function () {
      var me = f.getmatrixid(this.core.mtrx.client.credentials.userId)
      var pUsers = this.$store.state.users
      var self = this
      var members = _.map(self.chat.members, function (member) {
        var memberId = f.getmatrixid(member.state_key)
        return {
          id: pUsers[memberId].id,
          status: member.content.membership,
          image: pUsers[memberId].image,
          name: pUsers[memberId].name,
          source: pUsers[memberId].source
        }

      })
      return members.filter(n => n)
    }
  }),
}